<template>
	<view>
				
		<!-- tab标签 -->
		<uni-nav-bar :border="false" :fixed="true" :statusBar="true" @clickRight="openAddInput">
			<view class="flex align-center justify-center font-weight-bold course-tab">
				<view class="mx-5" @click="changeTab(index)" v-for="(item, index) in tabBars" :key="index"
				:class="tabIndex === index ? 'text-main font-lg' : 'font-md text-light-muted'">
					{{item.name}}
				</view>
			</view>
			
		</uni-nav-bar>
		
		
		<swiper :current="tabIndex" class="swiper-box" :style="{height:scrollH+'px'}" @change="onChangeTab">
			<!-- 全部 -->
		    <swiper-item>
		        <scroll-view scroll-y :style="'height:' + scrollH+'px;'"
				@scrolltolower="loadmoreEvent">
					<block v-for="(item,index) in list" :key="index">
						<!-- 课程内容 -->
						<course-list :item="item" :index="index"></course-list>
					</block>
					<load-more :loadmore="loadmore"></load-more>
		        </scroll-view>
		    </swiper-item>
			<!-- 未开始 -->
			<swiper-item>
			    <scroll-view scroll-y :style="'height:' + scrollH+'px;'">
					<block v-for="(item,index) in list" :key="index">
						<!-- 课程内容 -->
						<course-list :item="item" :index="index"></course-list>
					</block>
					<load-more :loadmore="loadmore"></load-more>
					
			    </scroll-view>
			</swiper-item>
			
			<!-- 学习中 -->
			<swiper-item>
			    <scroll-view scroll-y :style="'height:' + scrollH+'px;'">
					<block v-for="(item,index) in list" :key="index">
						<!-- 课程内容 -->
						<course-list :item="item" :index="index"></course-list>
					</block>
					<load-more :loadmore="loadmore"></load-more>
					
			    </scroll-view>
			</swiper-item>
			<!-- 已学完 -->
			<swiper-item>
			    <scroll-view scroll-y :style="'height:' + scrollH+'px;'">
					<block v-for="(item,index) in list" :key="index">
						<!-- 课程内容 -->
						<course-list :item="item" :index="index"></course-list>
					</block>
					<load-more :loadmore="loadmore"></load-more>
					
			    </scroll-view>
			</swiper-item>
			
		</swiper>
		
	</view>
</template>

<script>
	
	const demo = [
		{
			title:"高二上学期语文知识内容讲解, 重点知识串讲, 考前冲刺练习",
			classhour: 88,
			study_status: 1,
			teacher: "李老师",
			pay_status: 2
		},{
			title:"高三数学精品冲刺课程",
			classhour: 18,
			study_status: 1,
			teacher: "张老师",
			pay_status: 2
		},{
			title:"高二上学期语文知识内容讲解，重点知识串讲，考前冲刺练习",
			classhour: 55,
			study_status: 1,
			teacher: "李老师",
			pay_status: 2
		}
	];
	
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
	import loadMore from '@/components/common/load-more.vue';
	import hotCate from '@/components/news/hot-cate.vue';
	import topicList from '@/components/news/topic-list.vue'
	import courseList from '@/components/common/course-list.vue'
	
	export default {
		components:{
			uniNavBar,
			courseList,
			loadMore,
			hotCate,
			topicList
		},
		data() {
			return {
				scrollH: 500,
				tabIndex:0,
				tabBars:[{
					name:"全部"
				},{
					name: "未开始"
				},{
					name: "学习中"
				},{
					name: "已学完"
				}],
				//关注列表
				list:[
					
				],
				loadmore:"上拉加载更多",
				hotCate:[{
					name:"关注",
				},{
					name:"推荐"
				},{
					name: "军事"
				}],
				topicList:[{
					cover:"/static/demo/user2-160x160.jpg",
					title:"#话题名称#",
					desc:"#话题描述#",
					today_count:0,
					news_count:10
				},
				{
					cover:"/static/demo/user2-160x160.jpg",
					title:"#话题名称#",
					desc:"#话题描述#",
					today_count:0,
					news_count:10
				},
				{
					cover:"/static/demo/user2-160x160.jpg",
					title:"#话题名称#",
					desc:"#话题描述#",
					today_count:0,
					news_count:10
				},
				{
					cover:"/static/demo/user2-160x160.jpg",
					title:"#话题名称#",
					desc:"#话题描述#",
					today_count:0,
					news_count:10
				}
			  ]
			}
		},
		onLoad() {
			uni.getSystemInfo({
				success:(res)=>{
					this.scrollH = res.windowHeight - res.statusBarHeight - 44;
				}
			    })
			//加载测试数据
			this.list = demo
		},
		methods: {
			//打开发布页
			openAddInput(){
				uni.navigateTo({
					url:'../add-input/add-input'
				});
			},
			//切换选项卡
			changeTab(index){
				this.tabIndex = index
			},
			//滑动
			onChangeTab(e){
				//切换当前索引
				this.tabIndex = e.detail.current
			},
			//顶踩操作
			doSupport(e){
				
				//拿到当前对象
				let item = this.list[e.index];
				let msg = e.type === 'support' ? '顶' : '踩';
				
				//判断之前是否已经顶踩
				if(item.support.type === ''){
					item.support[e.type+'_count']++;
				} else if(item.support.type === 'support' && e.type === 'unsupport'){
					//顶 -1
					item.support.support_count--;
					//踩 +1
					item.support.unsupport_count++;
				} else if(item.support.type === 'unsupport' && e.type === 'support'){
					//之前踩了
					// 顶+1
					item.support.support_count++;
					// 踩-1
					item.support.unsupport_count--;
				}
				
				item.support.type = e.type;
				uni.showToast({title: msg + '成功'});
			},
			loadmoreEvent(){
				//验证当前是否处于可加载状态
				if(this.loadmore !== '上拉加载更多') return;
				//设置加载状态
				this.loadmore = '加载中...';
				//模拟请求数据
				setTimeout(()=>{
					//加载数据
					this.list = [...this.list, ...this.list];
					//设置加载状态
					this.loadmore = '上拉加载更多';
				},2000)
			},
			openSearch(){
				uni.navigateTo({
					url: '../search/search?type=topic',
					
				});
			}
			
		}
	}
</script>

<style>
	.course-tab {
		width: 850rpx;
		margin-left: -226rpx;
	}

</style>
